import Vue from 'vue'
import Router from 'vue-router'
import Chatroom from '@/components/Chatroom'

Vue.use(Router)

export default new Router({
    // mode: 'history',
    // base: process.env.BASE_URL,
    routes: [{
        path: '/',
        name: 'Chatroom',
        component: Chatroom
    }]
})


