<template>
  <div class="chatroom">
    <div class="container">
      <div class="modal is-active" v-show="isOpen">
        <div class="modal-background"></div>
        <div class="modal-content">
          <h1>輸入姓名</h1>
          <div class="field">
            <input class="input" type="text" v-model.trim="userName" @keydown.enter="setUserName($event)">
          </div>
          <div class="field">
            <button class="button" @click="setUserName($event)">送出</button>
          </div>
        </div>
      </div>

      <div class="chatbox">
        <div class="chat-header">
          <nav class="level is-mobile">
            <div class="level-center">
              <div class="level-item">
                <h1>Chatroom</h1>
              </div>
            </div>
            
          </nav>
          <div class="notice">
            <div class="notice-text"><h1>{{ userName }}</h1></div> 
          </div>
        </div>

        <ChatBody :messages="messages" :isUpload="isUpload" :progress="progress" :userName="userName"/>
        <ChatFooter :userName="userName" @updateUpload="updateUpload"/>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from '@/config/firebaseConfig.js';
const messageRef = firebase.database().ref('/message/');

export default {
  name: 'Chatroom',
  components: {
    ChatBody: () => import('@/components/layout/Body'),
    ChatFooter: () => import('@/components/layout/Footer'),
  },
  data: () => ({
    userName: '',
    message: '',
    messages: null,
    isOpen: true,
    isUpload: false,
    progress: 0,
  }),
  mounted() {
    this.getMessage();
  },
  methods: {
    setUserName (e) {
      if (e.shiftKey || e.keyCode === 229) {
        return false;
      }
      if (this.userName.length <= 1 && this.userName.trim() == '') {
        e.preventDefault();
        return false;
      }
      this.isOpen = false;
    },
    getMessage () {
      const vm = this;  
      messageRef.on('value', function(snapshot) {
        vm.messages = snapshot.val();
        console.log(vm.messages)
      })
    },
    updateUpload (param) {
      this.progress = param.progress;
      this.isUpload = param.isUpload;
    }
  },
}
</script>



